import React, { useState, useRef } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { FiUser, FiPhone, FiMapPin, FiMessageSquare, FiChevronDown, FiChevronUp } from 'react-icons/fi';
import { motion, AnimatePresence } from 'framer-motion';

const Form = () => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState(null);
  const [agreementOpen, setAgreementOpen] = useState(false);
  const scrollToAgreement = useRef(null);

  const formik = useFormik({
    initialValues: {
      name: '',
      phone: '',
      address: '',
      comment: '',
      agree: false
    },
    validationSchema: Yup.object({
      name: Yup.string().required(' '),
      phone: Yup.string()
        .matches(/^\+7\d{10}$/, ': +79881234568')
        .required(' '),
      address: Yup.string().required('  '),
      agree: Yup.boolean().oneOf([true], '  ')
    }),
    onSubmit: async (values) => {
      console.log('onSubmit   :', values);
      setIsSubmitting(true);
      try {
        console.log('  :', 'https://script.google.com/macros/s/AKfycbzYEip5RobQhoJpWoMlm6FffvbmH0OSz2Lg55Mwe8qIm03bano5TPhBfEZJAVRobaZ2/exec');
        
        const response = await fetch('https://script.google.com/macros/s/AKfycbzYEip5RobQhoJpWoMlm6FffvbmH0OSz2Lg55Mwe8qIm03bano5TPhBfEZJAVRobaZ2/exec', {
          method: 'POST',
          headers: { 
            'Content-Type': 'application/json; charset=utf-8'
          },
          body: JSON.stringify({
            name: values.name,
            phone: values.phone,
            address: values.address,
            comment: values.comment
          })
        });
        
        console.log(' :', response.status, response.statusText);
        
        if (response.ok) {
          setSubmitStatus('success');
          formik.resetForm();
        } else {
          throw new Error(' ');
        }
      } catch (error) {
        console.error('  :', error);
        setSubmitStatus('error');
      } finally {
        setIsSubmitting(false);
      }
    }
  });

  return (
    <div className="max-w-md mx-auto bg-white p-6 rounded-lg shadow-md">
      {submitStatus === 'success' && (
        <div className="mb-4 p-3 bg-green-100 text-green-700 rounded">
            !
        </div>
      )}
      
      {submitStatus === 'error' && (
        <div className="mb-4 p-3 bg-red-100 text-red-700 rounded">
           .   .
        </div>
      )}

      <form onSubmit={formik.handleSubmit}>
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">
            <FiUser className="inline mr-1" />
            
          </label>
          <input
            type="text"
            name="name"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder=" "
            value={formik.values.name}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          {formik.touched.name && formik.errors.name && (
            <div className="text-red-500 text-sm mt-1">{formik.errors.name}</div>
          )}
        </div>

        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">
            <FiPhone className="inline mr-1" />
            
          </label>
          <input
            type="tel"
            name="phone"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="+79991234567"
            value={formik.values.phone}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          {formik.touched.phone && formik.errors.phone && (
            <div className="text-red-500 text-sm mt-1">{formik.errors.phone}</div>
          )}
        </div>

        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">
            <FiMapPin className="inline mr-1" />
             
          </label>
          <input
            type="text"
            name="address"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder=", , "
            value={formik.values.address}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
          {formik.touched.address && formik.errors.address && (
            <div className="text-red-500 text-sm mt-1">{formik.errors.address}</div>
          )}
        </div>

        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-1">
            <FiMessageSquare className="inline mr-1" />
            
          </label>
          <textarea
            name="comment"
            rows="3"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder=" "
            value={formik.values.comment}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
          />
        </div>

        <div className="mb-4">
          <label className="flex items-start">
            <input
              type="checkbox"
              name="agree"
              className="mt-1 mr-2"
              checked={formik.values.agree}
              onChange={formik.handleChange}
            />
            <span className="text-sm">
                     
            </span>
          </label>
          {formik.touched.agree && formik.errors.agree && (
            <div className="text-red-500 text-sm mt-1">{formik.errors.agree}</div>
          )}
          
          {/*    */}
          <div className="mt-2 border rounded-lg overflow-hidden">
            <button
              type="button"
              ref={scrollToAgreement}
              className="w-full flex items-center justify-between p-3 bg-gray-50 hover:bg-gray-100 text-left"
              onClick={() => {
                setAgreementOpen(!agreementOpen);
                if (!agreementOpen) {
                  setTimeout(() => {
                    scrollToAgreement.current.scrollIntoView({ 
                      behavior: 'smooth', 
                      block: 'nearest'
                    });
                  }, 300);
                }
              }}
            >
              <span className="font-medium text-gray-700">
                   
              </span>
              {agreementOpen ? 
                <FiChevronUp className="text-gray-500" /> : 
                <FiChevronDown className="text-gray-500" />
              }
            </button>
            
            <AnimatePresence>
              {agreementOpen && (
                <motion.div
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: "auto", opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.3 }}
                  className="p-4 bg-white text-sm text-gray-600 overflow-hidden"
                >
                  <h3 className="font-bold mb-2">1.  </h3>
                  <p className="mb-3">
                               
                       152-     27.07.2006 .
                  </p>
                  
                  <h3 className="font-bold mb-2">2.  </h3>
                  <p className="mb-3">
                          : 
                    , ,  ,   , 
                      ,    ,  
                      .
                  </p>
                  
                  <h3 className="font-bold mb-2">3.  </h3>
                  <ul className="list-disc pl-5 mb-3 space-y-1">
                    <li>    </li>
                    <li>     </li>
                    <li>     </li>
                    <li>  </li>
                  </ul>
                  
                  <h3 className="font-bold mb-2">4.  </h3>
                  <p className="mb-3">
                            
                     ,     .
                  </p>
                  
                  <h3 className="font-bold mb-2">5.  </h3>
                  <p className="mb-3">
                             
                       .
                  </p>
                  
                  <h3 className="font-bold mb-2">6.  </h3>
                  <p>
                     (),      ,   
                        ,     
                        .
                  </p>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        </div>

        <button
          type="submit"
          disabled={isSubmitting || !formik.values.agree}
          className={`w-full text-white font-bold py-2 px-4 rounded transition ${
            isSubmitting || !formik.values.agree 
              ? 'bg-gray-400 cursor-not-allowed' 
              : 'bg-blue-600 hover:bg-blue-700'
          }`}
        >
          {isSubmitting ? '...' : ' '}
        </button>
      </form>
    </div>
  );
};

export default Form;